<template>
  <div class="content">
    <section class="headMean">
      <van-dropdown-menu>
        <van-dropdown-item v-model="acitve" :options="this.getRespone.headMean[0].list" />
        <van-dropdown-item v-model="acitve1" :options="this.getRespone.headMean[1].list" />
        <van-dropdown-item v-model="acitve2" :options="this.getRespone.headMean[2].list" />
      </van-dropdown-menu>
    </section>
    <div class="tips">
      <p class="used">在用</p>
      <div v-for="(item,index) of data" :key="index" :style="{'background':item.color}">
        <!-- 1: 占用；2：空闲；3：清扫；4：维修；5：超时；6呼叫；7、休息房 -->
        <p>{{item.houseType == 1 ? '按摩':item.houseType == 2 ? '空闲':item.houseType == 3 ? '清扫':item.houseType == 4 ? '特殊':item.houseType == 5 ? '超时':item.houseType == 6 ? '呼叫':'休息'}}</p>
      </div>
    </div>
    <div class="line">
      <p>已开包房：20</p>
      <p>空闲包房：30</p>
    </div>
    <section class="houseStateContent">
      <div class="houseContent">
        <div
          class="houseState"
          v-for="(item,index) of data"
          :key="index"
          :style="{'background':item.bgColor,'color':item.color}"
          @click="houseDetail(item.houseType)"
        >
          <p class="houseNum">{{item.houseNum}}</p>
          <p>{{item.region}}</p>
          <p
            v-show="item.card"
          >{{item.startedTime || item.usedTime}} {{item.surplsTime != 0 ? item.surplsTime:'-'+ item.abnormalTime}}</p>
          <p>{{item.houseType == 2 ? '空房':item.houseType == 3 ? '清扫房':item.houseType == 4 ? '维修房':''}}</p>
          <p v-if="item.houseType == 4">备注：{{item.remark}}</p>
          <div class="card">
            <section v-for="(e,idx) of item.card" :key="idx">
              <p v-show="e" v-if="idx <= 3">{{idx == 3 ? e+'...':e}}</p>
            </section>
          </div>
          <div class="srvice" v-show="item.houseType == 1">
            <p>技师：</p>
            <section v-for="(el,idxs) of item.serviceCard" :key="idxs">
              <p v-show="el" v-if="idxs <= 0">{{idxs == 0 ? el+'...':el+','}}</p>
            </section>
          </div>
          <p class="currentTime" v-show="item.card">{{currentTime}}</p>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
export default {
  data() {
    return {
      acitve: 0,
      acitve1: 10,
      acitve2: 20,
      currentTime: "",
      data: []
    };
  },
  mounted() {
    var _this = this; //声明一个变量指向Vue实例this，保证作用域一致
    this.timer = setInterval(function() {
      _this.currentTime = new Date().getHours() + ":" + new Date().getMinutes(); //修改数据date
    }, 0);
    _this.changeData();
  },
  methods: {
    changeData() {
      this.getRespone.houseInfo.forEach(e => {
        if (e.card != undefined && typeof e.card == "string") {
          e.card = e.card.split(",");
        }
        if (e.serviceCard != undefined && typeof e.serviceCard == "string") {
          e.serviceCard = e.serviceCard.split(",");
        }
      });
      this.data = this.getRespone.houseInfo;
    },
    houseDetail(typeId) {
      //  1: 按摩房；2：空闲；3：清扫；4：特殊；5：超时；6呼叫；7：休息房
      if (typeId == 1) {
        this.$router.push({ path: "/spaRoomDetail" });
      } else if (typeId == 2) {
        this.$router.push({ path: "/freeRoomDetail" });
      } else if (typeId == 3){
        this.$router.push({ path: "/clearRoomDetail" });
      } else if (typeId == 4){
        this.$router.push({ path: "/specialRoomDetail" });
      }  else if (typeId == 5){
        this.$router.push({ path: "/overTimeRoomDetail" });
      } else if (typeId == 6){
        this.$router.push({ path: "/callRoomDetail" });
      } else if (typeId == 7){
        this.$router.push({ path: "/restRoomDetail" });
      }
    }
  },
  beforeDestroy() {
    if (this.timer) {
      clearInterval(this.timer); // 在Vue实例销毁前，清除我们的定时器
    }
  }
};
</script>

<style lang="scss" scoped>
.line {
  @include fr(flex-start);
  @include sc(0.8rem, $fColor);
  padding: 0rem 0.8rem 0.3rem 0.8rem;
  font-weight: bold;
  p:first-child {
    margin-right: 1.2rem;
  }
}
.tips {
  @include grid(7, auto);
  @include sc(0.8rem, $fc);
  padding: 0.5rem 0.8rem;
  .used {
    background-color: #2087f1;
    background-image: linear-gradient(90deg, #2087f1 25%, #ffb84d 100%);
    padding: 0.2rem 0.3rem;
    margin-right: 0.2rem;
    border-radius: 0.3rem;
  }
  div {
    padding: 0.2rem 0.3rem;
    margin-right: 0.2rem;
    border-radius: 0.3rem;
  }
}
.houseStateContent {
  padding: 0rem 0.8rem;
  .houseContent {
    display: grid;
    grid-template-columns: repeat(3, 1fr); /* 相当于 1fr 1fr 1fr */
    grid-template-rows: repeat(auto, 1fr); /* fr单位可以将容器分为几等份 */
    grid-column-gap: 0.4rem;
    grid-row-gap: 0.4rem;
    grid-auto-flow: row;
    .houseState {
      @include fCol(flex-start, flex-start);
      @include wh(auto, 9rem);
      text-align: left;
      border-radius: 0.4rem;
      padding: 0.3rem;
      position: relative;
      .houseNum {
        font-size: 0.8rem;
        font-weight: bold;
        margin-bottom: 0.2rem;
      }
      p {
        font-size: 0.7rem;
      }
      .currentTime {
        margin: 0rem 0rem 0rem -0.6rem;
        width: 100%;
        text-align: right;
        position: absolute;
        bottom: 0;
      }
      .card {
        @include grid(2, auto);
        margin-top: 0.2rem;
        p:first-child {
          font-weight: normal;
        }
      }
      .srvice{
        @include fw(flex-start);
        @include wh(100%,auto);
        padding-top: 0.2rem;
        margin-top: 0.2rem;
        border-top: 0.02rem solid $fc;
      }
    }
  }
}
</style>